@tailwind base;
@tailwind components;
@tailwind utilities;
/* ==============================================
.p-calendar
============================================== */
.p-calendar {
  position: relative;
}
.p-calendar__loading {
  position: absolute;
  z-index: 1000;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: calc(100% - 72px);
  background: rgba(0, 0, 0, 0.5);
  display: none;
}
.p-calendar__loading-icon {
  position: relative;
}
.p-calendar__loading::before {
  content: "";
  display: block;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  border-radius: 5px;
  background: white;
}
.p-calendar__loading::after {
  content: "";
  display: block;
  position: fixed;
  left: 50%;
  top: 50%;
  translate: -50% -50%;
  width: 32px;
  height: 32px;
  border-radius: 20px;
  border: 4px solid #60ABB9;
  border-right: 4px solid white;
  animation: rotate 1s infinite linear;
}
@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.p-calendar__label {
  font-size: 32px;
  letter-spacing: 0.07em;
  font-weight: 700;
  padding-bottom: 24px;
}
.p-calendar__move {
  position: absolute;
  z-index: 999;
  top: 0;
  width: 50px;
  height: 50px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
}
.p-calendar__move::before, .p-calendar__move::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 13px;
  height: 3px;
  background-color: #333;
}
.p-calendar__move.js-prev {
  right: 60px;
}
.p-calendar__move.js-prev::before {
  transform-origin: top left;
  transform: translate(-50%, -50%) rotate(45deg);
}
.p-calendar__move.js-prev::after {
  transform-origin: bottom left;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.p-calendar__move.js-next {
  right: 0;
}
.p-calendar__move.js-next::before {
  transform-origin: top right;
  transform: translate(-50%, -50%) rotate(-45deg);
}
.p-calendar__move.js-next::after {
  transform-origin: bottom right;
  transform: translate(-50%, -50%) rotate(45deg);
}
.p-calendar__body {
  position: relative;
  display: flex;
  gap: 30px;
}
.p-calendar__item {
  flex: 1;
}
.p-calendar__table {
  width: 100%;
  border-collapse: collapse;
}
.p-calendar__day, .p-calendar__date {
  border: 1px solid #333;
  width: 14.2857142857%;
}
.p-calendar__day {
  height: 42px;
  background-color: #777;
  text-align: center;
  color: #FFF;
  font-size: 20px;
  font-weight: 600;
}
.p-calendar__date .js-cell {
  display: block;
  width: 100%;
  min-height: 125px;
  padding: 6px 0;
}
.p-calendar__date .day {
  font-size: 20px;
  font-weight: 600;
  display: block;
  width: 100%;
  text-align: right;
  padding-right: 10px;
}